.player-search {
    width: 80%;
    position: absolute;
    z-index: 3;
    transition: .9s ease;
}
.player-search--empty {
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.player-search--full {
    top: 25px;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
}
.player-search-field {
    width: 100%;
    transition: .5s ease;
    border-radius: 6px;
    outline: none;
    background-color: rgba(200,200,200, .5);
    border: 2px solid rgba(200,200,200, .5); 
    color: #fff;   
}
.player-search--empty .player-search-field {
    padding: 15px;
    font-size: 28px;
}
.player-search--full .player-search-field {
    font-size: 18px;
    padding: 12px;
}
.player-list {
    transition: .5s ease;
    max-height: 450px;
    overflow-y: auto;
}
.player-list-items {
    transition: opacity .5s ease;
}
.player-list--empty {
    opacity: 0;
}
.player-list--full {
    opacity: 1;
}
.player-list--short {
    flex-basis: 65%;
}
.player-list--wide {
    flex-basis: 100%;
}
.player-list-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 10px;
}
.player-track {
    display: flex;
    align-items: center;
    justify-content: flex-start
}
.play-image {
    width: 50px;
}
.pause-image {
    width: 15px;
}
.pause-image,
.play-image {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 2;
}
.play-icon,
.pause-icon {
    width: 25px;
    position: relative;
    display: block;
    height: 25px;
    overflow: hidden;
    margin-right: 5px;
}
.player-list-item{
    padding: 8px;
}
.player-list-item:not(:last-child) {
    border-bottom: 1px solid rgba(210, 210, 210, .7);
}
.player-track-title {
    font-weight: 700;
    margin-bottom: 3px;
    color: #fff;
}
.player-list-artist {
    font-weight: 300;
    color: #fff;
}
.player-control {
    transition: .5s ease;
}
.player-control--short {
    flex-basis: 0;
    opacity: 0;
}
.player-control--wide {
    flex-basis: 45%;
    opacity: 1;
}
.track-image {
    max-width: 250px;
}
.player-control-tracks {
    display: flex;
    flex-direction: column;
}
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
    vertical-align: baseline;
    box-sizing: border-box;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
    display: block;
    box-sizing: border-box;
}
body {
    line-height: 1;
    box-sizing: border-box;
}
ol, ul {
    list-style: none;
    box-sizing: border-box;
}
blockquote, q {
    quotes: none;
    box-sizing: border-box;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
    content: none;
    box-sizing: border-box;
}
table {
	border-collapse: collapse;
    border-spacing: 0;
    box-sizing: border-box;
}
/* scrollbar */
::-webkit-scrollbar {
	width: 6px;
	background: rgba(51, 51, 51, 0.4);
}

/* Track */
::-webkit-scrollbar-track {
	border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
	border-radius: 10px;
	background: rgba(255, 255, 255, 0.6);
	min-height: 120px;
}

::-webkit-scrollbar-thumb:window-inactive {
	background: rgba(255, 255, 255, 0.2);
}
html, body {
    height: 100%;
}
body {
    min-width: 320px;
    overflow-x: hidden;
    font-family: 'Lato', sans-serif;
}
.player-root {
    min-height: 100%;
    position: relative;
    background-image: url(/static/media/background.40a211f0.jpg);
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;   
    padding: 25px;
}
.player-controls {
    padding-top: 110px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 82%;
    margin: 0 auto;
}
